.context-menu {
  display: flex;
  position: absolute;
  background-color: #111;
  color: #ccc;
  flex-direction: column;
  padding: 0.5rem;
  width: 100px;
  z-index: 99;
}

.context-menu .context-item {
  padding: 0.3rem 0.3rem;
}

.context-menu .context-item:hover {
  background-color: #333;
  cursor: default;
}
